<template>
    <div :class="{sixteen: shuffledDeck.length == 16, twentyfive: shuffledDeck.length == 24, thirtysix: shuffledDeck.length == 36, thirtysixwrapper: shuffledDeck.length ==36,twentyfivewrapper: shuffledDeck.length ==24}" class="wrapper-div">
        <div @click="flipCard(card)" :class="{noclick: noClick}" class="card" v-for="card in shuffledDeck" :key="card.id">
            <div :class="{faceActive: card.flipped}" class="face">
                <div class="front">
                    <img :src="card.content" />
                </div>
                <div class="back">

                </div>
            </div>
        </div>
    </div>
</template>

<script>

import cardsArray from '@/data/cards.js'
import { ref, onMounted } from 'vue'

export default{
    props: {
        cardNum: Number
    },
    setup(props, context){
        onMounted(()=>{
            getCurrentDeck()
            shuffledDeck.value.forEach(card => card.flipped = false)    
        })
        let currentDeck = ref([])
        const sixteenDeck = cardsArray.slice(0, 16)
        const twentyfiveDeck = cardsArray.slice(0, 24)
        const thirtysixDeck = [...cardsArray]
        const maxMoves = ref(1)
        function getCurrentDeck(){
            if(props.cardNum === 24){
                currentDeck.value = twentyfiveDeck
                maxMoves.value = 20
            }
            else if(props.cardNum === 36){
                currentDeck.value = thirtysixDeck
                maxMoves.value = 25
            } else {
               currentDeck.value = sixteenDeck
               maxMoves.value = 16
            }
        }
        getCurrentDeck()
        function shuffleDeck(deck){
            for(let i = deck.length; i >= 0; i--){
                const randomIndex = Math.floor(Math.random() * (i+1))
                let oldValue = deck[i]    
                deck[i] = deck[randomIndex]
                deck[randomIndex] = oldValue
            }  
            return deck
        }
        
        const shuffledDeck = ref([...shuffleDeck(currentDeck.value).filter(element => element != undefined)])
        console.log(shuffledDeck.value)
        
        function flipCard(card){
            if(card.flipped != true){
            card.flipped = true
            storeCard(card, counter.value)
            if(counter.value > 1)
                counter.value = 0
            counter.value++
            }
            
        }
        const noClick = ref(false)
        const twoCards = []
        function storeCard(card, counter){
            if(counter==2)
            twoCards.length = 0
            twoCards.push(card)
            if(twoCards.length > 1){
                if(twoCards[0].matches != twoCards[1].matches){
                    noClick.value = true
                    maxMoves.value--
                    if(maxMoves.value <= 0){
                        console.log('You Lose')
                        context.emit('lose')
                    }
                    setTimeout(()=>{
                        twoCards[0].flipped = false
                        twoCards[1].flipped = false
                        noClick.value = false
                    }, 800)

                }else{
                    correctGuesses.value--
                }
                console.log(correctGuesses.value)
                if(correctGuesses.value <= 0) 
                context.emit('win')
            }
            
        }
        const correctGuesses = ref(shuffledDeck.value.length/2)
        const counter = ref(0)
        
        
        return{
            shuffledDeck,
            flipCard,
            noClick
        }
    },
    name: 'CardGrid'
}
</script>


