<template>
<div class="main">
  <div  class="game-display">
    <CardGrid @win="refresh('You Win')" @lose="refresh('You Lost')" :cardNum="gameState.cardNumState" v-if="gameState.gameStarted" />
    <button @click="startGame()" v-if="!gameState.gameStarted" class="start-game-button">Start game</button>
  </div>
  <div v-if="!gameState.gameStarted" class="select-wrapper">
    <CardNumSelect   :class="{isActive: gameState.cardNumState==16}" @cardNumEmit="getCardNum($event)" cardNumber=16 />
    <CardNumSelect   :class="{isActive: gameState.cardNumState==24}" @cardNumEmit="getCardNum($event)" cardNumber=24 />
    <CardNumSelect   :class="{isActive: gameState.cardNumState==36}" @cardNumEmit="getCardNum($event)" cardNumber=36 />
  </div>
</div>
<div :class="{lose: displayText == 'You Lost',win: displayText == 'You Win'}" v-if="afterGameScreen" class="cover">
<div v-if="afterGameScreen" class="display-text">{{displayText}}</div>
</div>
</template>

<script>

import CardGrid from '@/components/CardGrid.vue'
import CardNumSelect from '@/components/CardNumSelect.vue'
import { reactive, ref } from '@vue/reactivity'



export default {
  name: 'App',
  components:  {
     CardNumSelect,
     CardGrid,
  },
  setup(){
    const gameState = reactive({
      gameStarted: false,
      cardNumState: 16
    })
    
    function getCardNum($event){
       
        gameState.cardNumState = $event
  
    }
    function startGame(){
      gameState.gameStarted = true
      
    }
    const displayText = ref('')
    const afterGameScreen = ref(false)
    function refresh(string){
      displayText.value = string
      afterGameScreen.value = true
      setTimeout(()=>{
        gameState.gameStarted = false
        afterGameScreen.value = false
      }, 1500)
    }
    return{
      getCardNum,
      gameState,
      startGame,
      refresh,
      displayText,
      afterGameScreen
    }
  }

}
</script>

