      box-sizing: border-box;
      font-family: sans-serif;
    }
    .cover{
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.219);
      z-index: 10;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
    }
    .display-text{
      text-align: center;
      font-size: 4rem;
    

    }
    .lose{
      color: red;
    }
    .win{
      color: green;
    }
    #app{
      display: flex;
      justify-content: center;
      background: rgba(0, 0, 0, 0.226);;
    }
    .main{
      display: flex;
      height: 100vh;
      width: 100%;
      max-width: 1000px;
      flex-direction: column;
      background: white;
    }    
    .select-wrapper{
      flex-basis: 30%;
      display: flex;
      flex-direction: column;
    }
    .select-wrapper *{
      flex-basis: 100%;
    } 
    .game-display{
        width: 100%;
        flex-basis: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .isActive{
      background: black;
      color: white;
    }
    .start-game-button{
      font-size: 4rem;
      padding: 30px;
      background: black;
      color: white;
      border: none;
      box-shadow: 4px 4px 4px 1px rgba(0, 0, 0, 0.479);
      cursor: pointer;
      transition: 150ms;
      border-radius: 8px;
    }
    .start-game-button:hover{
      transform: scale(1.1)
    }
    
</style>
