<template>
    <div @click="sendCardNum"> <span> <span> Difficulty: </span> {{cardNumber}} cards </span> </div>
</template>


<script>


export default{
    name: 'CardNumSelect',
    props: {
        cardNumber: Number
    },
    setup(props, context){
        function sendCardNum(){
            context.emit('cardNumEmit', parseFloat(props.cardNumber))
        }

        return{
            sendCardNum
        }
    }
}   

</script>


<style scoped>
    div{
        display: flex;
        align-items: center;
        border-top: 1px solid black;
        transition: 300ms;
        cursor: pointer;
        padding: 20px;
    }
    div:hover{
       background: black;
      color: white;
    }
</style>